.hairtsag{
    background-color:rgb(50, 50, 187);
    width: 300px;
    height: 200px;
    border-radius:30px;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
        margin-right: 20px;
}
.container{
    display: flex;
    justify-content: center;

}
.blue{
    background-color: blue;
}
.orange{
    background-color: rgb(255, 81, 0)
}
.green{
    background-color: rgb(31, 172, 31);
}

.hairtsag h1 {
    font-size: 60px;
    font-weight: bold;
    margin: 0px;
} 
.hairtsag p{ 
    font-size: 32px;
}
.darah-heseg:active{
    transform: scale(0.97); 
}
.darah-heseg{
    border: 1px solid black;
    height: 65vh;
    width: 50vw;
    cursor: pointer;
    margin-top: 20px;
    margin-inline: 20px;
    border-radius: 30px;
    display: flex;
    justify-content: center;
    align-items: center; 
    font-size: 55px;
    transition: 0.5s;
}
.main{
    display: flex;
    flex-direction: column;
    align-items: center;
}